<template>
  <div class="auditConclusion">
    <div class="container">
      <div class="activeTitle">
        参赛成果审核
      </div>
      <div class="settingContent">
          <!-- 选择框 -->
        	<div class="requirement">
            <span>
              状态
              <el-select :clearable="clearable" v-model="statusValue" placeholder="请选择" class="selected" @change="selectStatus">
                <el-option   
                v-for="item in statusOption"
                :key="item.value1"
                :label="item.label1"
                :value="item.value1">
                </el-option>
              </el-select>
            </span>
            <span>
              类别
              <el-cascader
                :options="sortOption"
                v-model="sortValue"
                @change="selectType"
                placeholder="不限"
                @expand-change="selectType"
              >
              </el-cascader>
            </span>
            <span v-if="this.userInfo.type != 2">
              省份
              <el-select :clearable="clearable" v-model="cityDefault" placeholder="请选择" class="selected" @change="selectCity">
                <el-option   
                v-for="item in cityOptions"
                :key="item"
                :value="item">
                </el-option>
              </el-select>
            </span>
            <span>
              来源
              <el-select v-model="sourceValue" placeholder="请选择" class="selected" @change="selectFrom">
              <el-option
              v-for="item in sourceOptions"
              :key="item.value2"
              :label="item.label2"
              :value="item.value2">
              </el-option>
              </el-select>
            </span>
            <span>
              <el-date-picker
                v-model="timeValue"
                type="datetimerange"
                range-separator="至"
                start-placeholder="开始日期"
                @change="selectTime"
                value-format="timestamp"
                end-placeholder="结束日期">
              </el-date-picker>
            </span>
            <span class="search">
              <i class="el-icon-search"></i>
              <el-input v-model.trim="search" placeholder="请输入搜索内容" class="inputSearch"></el-input>
              <el-button type="primary" class="searchButton" @click="inputSearch">搜索</el-button>
            </span>
            <span class="clear" @click="clear">
              清除
            </span>
          </div>
          <!-- 内容组件 -->
          <subContent :optionArr='optionArr' @listenChildren='listenChildren'></subContent>
          <div class="page">
          <el-pagination
            background
            layout="prev, pager, next"
            :total="total"
            @current-change="handleCurrentChange"
            >
          </el-pagination>
		</div>   
      </div>
    </div>
  </div>
</template>
<style lang="less" scoped>
.auditConclusion{
   text-align: left;
  .container{
    margin:60px 0 0 260px;
    .activeTitle{
      color: #808080;
  	  height: 58px;
  	  font-size: 14px;
  	  line-height: 58px;
  	  padding-left: 50px;
  	  border-bottom: 1px solid #eee;
    }
    .settingContent{
      .requirement{
        text-align: left;
        font-size: 12px;
        padding:20px 50px;
        span{
          margin-right: 20px;
          display: inline-block;
          margin-bottom: 20px;
          .selected{
            margin-left: 8px;
            width: 120px;
          }
          .el-cascader{
            width: 120px;
          }
        }
        .clear {
          font-size: 14px;
          display: inline-block;
          width: 60px;
          height: 38px;
          border: 1px solid #409EFF;
          color: #409EFF;
          text-align: center;
          line-height: 38px;
          border-radius: 3px;
          cursor: pointer;
        }
        .search{
          .el-icon-search{
            position: relative;
            left: 26px;
            z-index: 2;
          }
          .inputSearch{
            width: auto;
            left: 3px;
          }
          .searchButton{
            font-size: 14px;
          }
        }
      }
    }
  }
  .page{
    margin-top: 50px;
    text-align: center;
  }
}
</style>
<script>
import subContent from '@/components/subContent'
import {mapState} from 'vuex';
const statusOption = [{
        value1: -1,
        label1: '不限'
      },{
        value1: 0,
        label1: '草稿'
      },{
        value1: 1,
        label1: '未审核'
      },{
        value1: 2,
        label1: '被通过'
      },{
        value1: 3,
        label1: '被驳回'
      }];
const sortOption = [{
        value: [],
        label: '不限',
        children:[{
          value: [],
          label: '不限'
        }]
      }]
const sourceOptions = [{
        value2: -1,
        label2: '不限'
      },{
        value2: 1,
        label2: '自主研发'
      },{
        value2: 2,
        label2: '合作研发'
      },{
        value2: 3,
        label2: '企业研发'
      }]
export default {
  name:'matchAllocation',
  data(){
    return{
      inputNums:'',
      statusOption: statusOption,//状态
      statusValue: '不限',
      cityDefault:'不限',//默认城市
      cityOptions:this.districtList,//城市列表
      sortOption:sortOption,//类别
      sortValue: "",
      sourceOptions: sourceOptions,
      sourceValue: '不限',//来源
      timeValue: '',//时间
      search: '',
      undercarriageMask: false,
      optionArr: [],//成果数据
      total: 0,
      blogId: "",
      state: 0 ,//将作品下架，状态码为0，反之为1
      pageNum: 1,
      dialogVisible: false,
      anothorResultInput: '',
      rejectInfo: '',
      clearable: true,
    }
  },
  computed: {
    ...mapState({
      userInfo:(state)=>state.mutations.userInfo
    })
  },
  watch: {
    userInfo(val){
      console.log('变化信息:',val.district);
    }
  },
  created() {
    this.updateToken()
    this.verifyToken()
    this.getCategory()
  },
  methods: {
    //判断管理员权限
    judegeIsAdmin(){
      if(this.userInfo.type == 2){
        this.cityDefault = this.userInfo.district
        console.log('变更省份2:',this.cityDefault)
        this.getAuditBlog()
      }else{
        this.getAuditBlog()
      }
    },
    //刷新token
    updateToken() {
      let accessToken = localStorage.getItem("accessToken")
      if (accessToken) {
        let url =this.api.configIp + this.net.updateToken;
        let params = {
          accessToken: accessToken
        }
        this.$fetch(url,params).then((res)=>{
          console.log('sucess:',typeof(res.data))
          if(res.success){
            this.$store.commit("saveUserInfo",JSON.parse(res.data))
            this.judegeIsAdmin()
          }else{
            this.judegeIsAdmin()
          }
        }).catch((err)=>{
          console.log('err:',err)
        })
      }
    },
    //验证token是否过期
    verifyToken() {
        let accessToken = localStorage.getItem("accessToken")
        if (accessToken) {
          let url =this.api.configIp + this.net.verifyToken;
          let params = {
            accessToken: accessToken
          }
          this.$fetch(url,params).then((res)=>{
            console.log('sucess:',res)
            if(!res.success){
              this.$message.warning("登录已经超时,需重新登陆");
              setTimeout(()=>{
                this.$router.push({name:'login'})
              },2000)
            }
          }).catch((err)=>{
            console.log('err:',err)
          })
        }
    },
    //选择状态
    selectStatus(val) {
      this.statusValue= val
      this.getAuditBlog();
    },
    //选择来源
    selectFrom(val) {
      this.sourceValue = val;
      this.getAuditBlog();
    },
    //选择省份
    selectCity(val) {
      this.cityDefault = val;
      this.getAuditBlog()
    },
    //选择时间
    selectTime(val) {
      this.timeValue = val;
      this.getAuditBlog();
    },
    //选择类别
    selectType(val) {
      this.sortValue = val;
      this.getAuditBlog();
    },
    inputSearch() {
      if(this.search==""){
        this.$message.warning('请输入搜索内容!')
        return
      }
      this.getAuditBlog();
    },
    //获取类别
    getCategory() {
      this.$post(`${this.url}/manage/category/getAllCategory`,
        {}
      )
      .then((response) => {
        if(response.code == 200 && response.data != null && response.data != 'null'){
          let data = response.data;
          
          const newTeam = function (data, parentId) {
            let itemArr = [];
            for (let i = 0; i < data.length; i++) {
              let node = data[i];

              if (node.parentCategoryId == parentId) {
                let newNode = {};
                newNode.value = node.categoryId;
                newNode.label = node.categoryName;
                itemArr.push(newNode);
                if(newTeam(data, node.categoryId).length != 0){
                  newNode.children = newTeam(data, node.categoryId);     
                }            
              }
            }
            return itemArr;              
          }
          
          this.sortOption = [...this.sortOption, ...newTeam(data, 0)];

        }else{
          this.$message.error('请求失败，请稍后重试');
        }
      })
    },
    getAuditBlog() {
        let state = this.statusValue;
        let tagTypeId = this.sortValue;
        let msgFrom = this.sourceValue;
        let time = this.timeValue;
        let blogTitle = this.search;
        let district = this.cityDefault;
        if(typeof(state) == "string" || state == -1){
          state = "";
        }

        if(!tagTypeId){
          tagTypeId = ["", ""];
        }

        if(tagTypeId.join("") == ""){
          tagTypeId = ["",""];
        }
        if(typeof(msgFrom) == "string" || msgFrom == -1){
          msgFrom = "";
        }
        if(district == '不限'){
          district = "";
        }
        if(!time){
          time = ["",""]
        }
        
        let params = {
					auditTerminal:'2',
					district:district,
          pageNum  : this.pageNum,
          pageSize : 10,
          auditState: state,
          tagTypeId1:tagTypeId[0],
          tagTypeId2:tagTypeId[1],
          msgFrom: msgFrom,
          startDate: time[0],
          endDate: time[1],
          blogTitle: blogTitle,
          orderByParam:"{auditState:1}"
        };
        console.log('查询参数:',JSON.stringify(params))
        this.$post(`${this.url}/manage/audit/getAllAuditBlog`,
          params
        )
        .then((res) => {
          if(res.code == 200 && res.data.result != null && res.data.result != 'null'){
            this.optionArr = res.data.result;
            this.total = res.data.total;
            this.pageNum = res.data.pageNum;
          }else{
            this.$message.error('请求失败，请稍后重试');
          }
        })
    },
    clear() {
      this.search = '';
      this.statusValue = -1;
      this.sortValue = '';
      this.sourceValue = '不限';
      this.timeValue = '';
      this.cityDefault='不限'
      this.getAuditBlog();
    },
    handleCurrentChange(val) {
      this.pageNum = val;
      this.getAuditBlog()
    },
    //监听子组件变化,重新刷新
    listenChildren(val){
      if(val){
        this.getAuditBlog()
      }
    }
  },
  components:{
    subContent,
  }
}
</script>


